<template>
  <div class="login">
    <div class="login-box">
      <div class="form-box">
        <div class="title">
          欢迎使用XXX管理系统
        </div>
        <!-- 表单模块, model是绑定表单数据 -->
        <!-- rules表单的验证规则 -->
         <a-form
           ref="formRef"
           :model="formState"
           :rules="rules"
         >
              <!-- 账号 -->
           <a-form-item  name="username">
             <a-input v-model:value="formState.username">
                <template #prefix>
               <user-outlined type="user" />
               </template>
             </a-input>
           </a-form-item>
           <!-- 密码 -->
            <a-form-item  name="password">
             <a-input v-model:value="formState.password">
                <template #prefix>
                  <LockOutlined />
               </template>
             </a-input>
           </a-form-item>
             <div class="btn-box">
                <a-button type="primary" @click="onSubmit">登陆</a-button>
                <a-button type="primary" style="margin-left:35px" @click="resetForm">重置</a-button>
           </div>
          </a-form>
      </div>
    </div>
  </div>
</template>
<script>
import { message } from 'ant-design-vue'
import { reactive, ref } from '@vue/reactivity'
import { UserOutlined, LockOutlined } from '@ant-design/icons-vue'
import { getCurrentInstance } from 'vue'
export default {
  components: {
    UserOutlined,
    LockOutlined
  },
  name: 'Login',
  setup () {
    // 定义获取表单元素的ref对象
    const formRef = ref()
    // 声明表单验证对象
    const formState = reactive({
      username: 'admin',
      password: '123456'
    })
    // 声明表单验证规则
    const rules = {
      username: [
        { required: true, message: '请输入用户名', trigger: 'blur' },
        { min: 4, max: 12, message: '名字为4~12位字符', trigger: 'blur' }
      ],
      password: [
        { required: true, message: '请输入密码', trigger: 'blur' },
        { min: 6, max: 18, message: '密码为6~18位字符', trigger: 'blur' }
      ]
    }
    // 重置表单的方法
    const resetForm = () => {
      formRef.value.resetFields()
    }
    // 把实例对象解构出来,方便使用全局的属性或方法
    const { proxy } = getCurrentInstance()
    // console.log(getCurrentInstance());
    // 点击登陆提交的处理方法
    const onSubmit = () => {
      // 调用表单验证的方法
      formRef.value.validate()
        .then(async value => {
        // res为main.js中response拦截器解析回来的数据
          const { data, meta } = await proxy.$http.post('login', value)
          if (meta.status != 200) return message.error(meta.msg)
          message.success(meta.msg)
          // 把token和用户名保存到本地
          window.sessionStorage.token = data.token
          window.sessionStorage.username = data.username
          proxy.$router.push('/home')
        }).catch(() => false)
    }
    return {
      formState,
      rules,
      formRef,
      resetForm,
      onSubmit
    }
  }
}
</script>
<style lang="less" scoped>
.login{
  background-color: #fff;
  // vh是相对单位,相对于屏幕
  height: 100vh;
  background-image: url('../assets/imgs/login_bg1.jpg');
  background-size: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .login-box{
    position: relative;
    width: 500px;
    height: 300px;
    background-color: rgba(0,0,0,.6);
    border-radius: 20px;
    padding: 0 20px;
    padding-top: 90px;
  }
  .title{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%,-50%);
    color: #fff;
    // width: 200px;
    padding: 0 20px;
    height: 40px;
    line-height: 40px;
    border-radius: 10px;
    background-color: #6e00ee26;
    font-size: 20px;
  }
    .btn-box{
      margin-left: 150px;
    }
}
</style>
